* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

main {
  width: 95%;
  max-width: 900px;
  margin: 0 auto;
  margin-top: 80px;
}

:root {
  background-color: black;
  --clr-btn: rgb(252, 255, 238);
  --clr-txt: rgb(36, 36, 36);
  --clr-btn-ignored: tomato;
  --clr-btn-selected: #86d10d;
  --clr-btn-disabled: #532f35;
}

.ghosts-list,
.btnEvidence-list {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
}

.ghosts-list {
  height: 300px;
  color: white;
  font-family: "Creepster", cursive;
  font-size: clamp(1rem, 8vw, 2rem);
}

.ghosts-list li {
  padding: 0.25em 0.5em;
}

.btnEvidence-list {
  margin: 0 auto;
  max-width: 600px;
}

.btnEvidence {
  cursor: pointer;
  border: none;
  width: 10ch;
  min-height: 75px;
  font-family: "Yomogi", cursive;
  font-size: 1.025rem;
  font-weight: bolder;
  padding: 0.75em 2em;
  margin: 0.25em;
  background-color: var(--clr-btn);
  color: var(--clr-txt);
}

.btnEvidence-ignored {
  background-color: var(--clr-btn-ignored);

  color: white;
}

.btnEvidence-selected {
  background-color: var(--clr-btn-selected);
  color: white;
}

.btnEvidence:disabled {
  background-color: var(--clr-btn-disabled);
  opacity: 50%;
  color: white;
  text-decoration: line-through;
  cursor: not-allowed;
}
